body {
    margin: 0 auto;
}

.container {
    background: #eee;
    width: 250px;
    height: 660px;
    overflow: hidden;
    display: inline-block;
    padding: 20px;
    margin: 5px;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.3);
}

.title {
    color: #333;
    text-decoration: none;
    cursor: pointer;
    text-align: left;
    width: 100%;
    font-size: 20px;
    height: 28px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.year {
    font-size: 16px;
}

.summary {
    height: 180px;
    padding: 8px;
    background: #fff;
    font-size: 15px;
    display: flex;
    align-items: center;
    line-height: 1.3;
}

.img {
    width: 250px;
    height: 320px;
    object-fit: cover;
    object-position: top center;
}

.genre_box {
    padding-left: 0;
    padding-top: 0;
    margin-top: 0;
}

.genres {
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 50px;
    list-style: none;
    display: inline-block;
    padding: 5px 10px;
    margin: 2px;
    font-size: 12px;
}